footer {
  background: var(--color-primary);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;

  a {
    color: var(--color-bg);
  }
}

.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 3rem;

  a {
    text-transform: capitalize;
  }
}

.footer__socials {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 4rem;

  .footer__socials-icon {
    font-size: 1.5rem;
  }

  a {
    border-radius: 0.7rem;
    color: var(--color-bg);
    display: flex;
    transition: var(--transition);

    &:hover {
      color: var(--color-white);
    }
  }
}

.footer__copyright {
  margin-bottom: 4rem;
  color: var(--color-bg);
}

// =============== MEDIA QUERIES ===============

@media screen and (max-width: 600px) {
  .permalinks {
    flex-direction: column;
    gap: 1.5rem;
  }

  .footer__socials {
    margin-bottom: 2.6rem;
  }
}
